import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../../store';
import { fetchCount } from './exampleAPI';

export interface ExampleState {
	value: number;
	status: 'success' | 'loading' | 'failed';
}

const initialState: ExampleState = {
	value: 0,
	status: 'success',
};

//     thunk      : `dispatch(incrementAsync(10))`.
//      .
export const incrementAsync = createAsyncThunk(
	'example/fetchCount',
	async (amount: number) => {
		const response = await fetchCount(amount);
		return response.data;
	}
);

export const exampleSlice = createSlice({
	name: 'example',
	initialState,
	//  "reducers"          
	reducers: {
		increment: (state) => {
			// Redux Toolkit    ""   .
			//       ,      Immer,
			//            
			state.value += 1;
		},
		decrement: (state) => {
			state.value -= 1;
		},
		//   PayloadAction    `action.payload`
		incrementByAmount: (state, action: PayloadAction<number>) => {
			state.value += action.payload;
		},
	},
	//  `extraReducers`    ,    ,
	//  ,  createAsyncThunk    .
	extraReducers: (builder) => {
		builder
			.addCase(incrementAsync.pending, (state) => {
				state.status = 'loading';
			})
			.addCase(incrementAsync.fulfilled, (state, action) => {
				state.status = 'success';
				state.value += action.payload;
			})
			.addCase(incrementAsync.rejected, (state) => {
				state.status = 'failed';
			});
	},
});

export const { increment, decrement, incrementByAmount } = exampleSlice.actions;

export const exampleReducer = exampleSlice.reducer;

//           
// .        React,    .
// :  `useSelector((state: RootState) => state.example.value)`
export const selectCount = (state: RootState) => state.example.value;
